body {
  margin: 0; }
// Section pour l'affichage des films en cartes

.card {
  height: 100%;
  display: flex;
  >.card-body {
    font-weight: normal;
    text-align: center;
    max-width: 20rem;
    min-width: 10rem;
    margin: 0.1rem; }
  .title {
    font-weight: bolder; }
  >.details {
    display: flex;
    justify-content: space-between;
    margin: 0.5rem;
    font-style: italic; }
  >.card-grid-view {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-left: 2rem;
    margin-top: 1rem;
    width: 75%; } }

// Section pour le menu de navigation navbar pour les link
.navbar-a {
  >a {
    color: rgba(255,255,255,.5);
    padding: .5rem;
    text-decoration: none;
    background: transparent;
    &:hover {
      text-decoration: none;
      color: #ffffff;
      background-color: transparent;
      text-decoration-style: solid; } } }
#boutonInstall {
    background-color: transparent;
    text-decoration-style: solid;
    border: solid gray;
    padding: .3em;
    color: rgba(255,255,255,.5);
    &:hover {
      color: #ffffff; } }










